const formValidate = (formEl: FormInstance) => {
    if (!formEl) return
    return new Promise((resolve,reject) => {
        formEl.validate(async (valid, fields) => {
            if (valid) {
               resolve('success')
            } else {
               reject('err')
            }
        })
    })
}
export const submitForm  = async (refList:Array<FormInstance>) => {
    let allPromise:any = []
    refList.forEach((item:any) => {
        allPromise.push(formValidate(item))
    })
    return Promise.all(allPromise)
}

