export const sortFunction = ((arr:Array<any>,key:string,boolean:boolean) => {
    if(arr && arr?.length){
        function compare(key:any,boolean:any):any{
            return function(a:any,b:any){//
                let val1 = a[key]
                let val2 = b[key]
                 if(boolean){
                     //
                     return Date.parse(val1) - Date.parse(val2)
                 }else {
                     //
                     return Date.parse(val2) - Date.parse(val1)
                 }
            }
        }
        return arr.sort(compare(key,boolean))
    }
    return arr
}) 

