export const transformTimeFormat = (time:any) => {
  if(time){
    const tmp = new Date(time)
    const year = tmp.getFullYear()
    const month = tmp.getMonth() + 1 < 10 ? `0${tmp.getMonth() + 1}` : tmp.getMonth() + 1
    const day = tmp.getDate() < 10 ? `0${tmp.getDate()}` : tmp.getDate()
    const hour = tmp.getHours() <10 ? `0${tmp.getHours()}` : tmp.getHours()
    const minutes = tmp.getMinutes() <10 ? `0${tmp.getMinutes()}` : tmp.getMinutes()
    const second = tmp.getSeconds() <10 ? `0${tmp.getSeconds()}` : tmp.getSeconds()
    return `${year}-${month}-${day} ${hour}:${minutes}:${second}`
  }else{
      return null
  }
}
//
export const debounce = (fn:Function,wait:number = 1000) => {
    let timer:any = null;
    return function(){
      if(timer != null) {
        clearTimeout(timer)
        timer = null
      }
      timer = setTimeout(() => {
        fn()
      },wait)
    }
  }

