/**
 * urlbase64
 * @param { String } imageUrl http://xxxx/xxx.png
 * @returns base64
 */
export const urlToBase64 = async (imageUrl: string) => {
return new Promise((resolve, reject) => {
    let canvas: HTMLCanvasElement | null = document.createElement('canvas');
    let ctx = canvas.getContext('2d');
    let img: HTMLImageElement | null = new Image();
    if (ctx && img && canvas) {
    img.crossOrigin = 'Anonymous'; // Canvas.toDataURL 
    img.src = imageUrl;
    img.onload = function () {
        if (!ctx || !img || !canvas) {
        return;
        }
        canvas.height = img.height;
        canvas.width = img.width;
        ctx.fillStyle = '#fff'; // canvas
        ctx.fillRect(0, 0, img.width, img.height);
        ctx.drawImage(img, 0, 0); // 
        const dataURL = canvas.toDataURL('image/png', 1); // Base64
        resolve(dataURL);
        canvas = null; // canvas
        img = null; // img
    };
    img.onerror = function () {
        reject(new Error(' ' + imageUrl));
    };
    }
});
};