import axios from 'axios';
import { Axios } from '@/utils/http';
import store from '@/store';

const state = {
  index_meta_data_all: {},
};

const getters = {};

const mutations = {
  set_dir_nav_datas(state: any, payload: { id: string; data: any }) {
    // id
    state.index_meta_data_all = {
      ...state.index_meta_data_all,
      [payload.id]: payload.data,
    };
  },
};

const actions = {
  action_set_dir_nav_data(
    ctx: any,
    payload: { id: string; refresh?: boolean; loading_type?: 'all', isNotInfoTip?: boolean }
  ) {
    // 
    // refreshtrue
    if (ctx.state.index_meta_data_all[payload.id] && !payload.refresh) {
      // 
      return true;
    }

    return Axios({
      method: 'get',
      url: `/index/getIndexMeta/${payload.id}`,
    })
      .then((res: any) => {
        if (!res.data?.data || JSON.stringify(res.data?.data) === '{}') {
          // meta
          return false;
        } else {
          // meta
          ctx.commit('set_dir_nav_datas', {
            id: payload.id,
            data: {
              id: payload.id,
              data: res.data?.data, // datameta
              meta: res.data?.data,
            },
          });
          !payload.isNotInfoTip && store.commit('POP_MESSAGE', { type: 'success', message: '' });
          return true;
        }
      })
      .catch((err: any) => {
        console.log('err:', err);
        store.commit('POP_MESSAGE', { type: 'error', message: '' });
        throw new Error(err);
      });
  },
};

export default {
  state,
  getters,
  mutations,
  actions,
};
